.btn,
.jumbotron,
.nav :hover {
  border-radius: 0px;
}

body {
  background: #343a40;
  min-height: 100vh;
}

footer {
  position: relative;
  left: 0px;
  right: 0px;
  bottom: 0px;
}

.nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.logo {
  font-weight: 500;
  font-size: 20px;
  color: white;
  margin-right: 25px;
}

@media (max-width: 575.98px) {
  .col-4 {
    -ms-flex: 0 0 100%;
    flex: 0 0 100%;
    max-width: 100%;
  }

  .nav-link {
    display: block;
    padding: .5rem .4rem;
  }
}

@media (min-width: 576px) and (max-width: 767.98px) {
  .col-4 {
    -ms-flex: 0 0 100%;
    flex: 0 0 100%;
    max-width: 100%;
  }

  .row {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    margin: 15px;
  }

  .nav-link {
    display: block;
    padding: .5rem .4rem;
  }
}

/* 768px */
@media (min-width: 768px) and (max-width: 991.98px) {
  .col-4 {
    -ms-flex: 0 0 50%;
    flex: 0 0 50%;
    max-width: 50%;
  }

  .nav-link {
    display: block;
    padding: .5rem .4rem;
  }
}

@media (min-width: 992px) and (max-width: 1199.98px) {
  .col-4 {
    -ms-flex: 0 0 33.333333%;
    flex: 0 0 33.333333%;
    max-width: 33.333333%;
  }

  .nav-link {
    display: block;
    padding: .5rem 1rem;
  }
}